@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
:root {
  --gray-500: #b8b6b4;
  --gray-600: #1B2838;
  --gray-800: #171a21;
}

* {
  font-family: "PT Sans", sans-serif;
}

html {
  background-color: var(--gray-800);
  font-size: 87.5%;
}
@media (min-width: 740px) {
  html {
    font-size: 93.75%;
  }
}

body {
  --webkit-font-smoothing: antialiased;
}

.container {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1140px) {
  .container {
    max-width: 1140px;
  }
}

/*
    Este documento foi desenvolvido com base no mesmo contido em: https://meyerweb.com/eric/tools/css/reset/
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.swiper {
  width: auto;
  height: 300px;
}
.swiper-newworld {
  background-size: cover;
  background-image: url("../../assets/new-world-2.webp");
  background-repeat: no-repeat;
  background-position: center;
  filter: brightness(0.8);
}
.swiper-newworld h2 {
  color: var(--gray-800);
  font-size: 2.3rem;
  margin-bottom: 1rem;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 740px) {
  .swiper-newworld h2 {
    text-align: left;
  }
}
.swiper-newworld p {
  color: var(--gray-800);
  font-size: 1.5rem;
  text-shadow: 4px 4px 2px #969696;
  text-align: center;
  padding: 0 2rem;
}
@media (min-width: 740px) {
  .swiper-newworld p {
    text-align: left;
  }
}
.swiper-hogwarts {
  background-size: cover;
  background-image: url("../../assets/hogwarts-legacy.jpg");
  background-repeat: no-repeat;
  background-position: center;
  filter: brightness(0.8);
}
.swiper-hogwarts h2 {
  color: var(--gray-500);
  font-size: 2.35rem;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 740px) {
  .swiper-hogwarts h2 {
    text-align: left;
  }
}
.swiper-hogwarts p {
  display: none;
}
.swiper-hogwarts a {
  display: none;
}
.swiper-mario {
  background-size: cover;
  background-image: url("../../assets/super_mario.jpg");
  background-repeat: no-repeat;
  background-position-y: top;
}
.swiper .swiper-button-prev:after, .swiper .swiper-rtl .swiper-button-next:after,
.swiper .swiper-button-next:after, .swiper .swiper-rtl .swiper-button-prev:after {
  color: #fff;
}
.swiper .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  background-color: #fff;
}

:root {
  --background-top-header: var(--gray-600);
  --font-brand-color: var(--gray-500);
  --font-menu-color: var(--gray-500);
}

.header {
  align-items: center;
  background-color: var(--background-top-header);
  display: flex;
  padding: 2.35rem 0;
}
@media (min-width: 740px) {
  .header {
    height: 5rem;
    padding: 0;
  }
}
.header__top-bar {
  padding: 0 0.85rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: 1fr;
}
@media (min-width: 740px) {
  .header__top-bar {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
.header__top-bar h1 {
  color: var(--font-brand-color);
  font-size: 1.2rem;
  text-align: center;
}
@media (min-width: 740px) {
  .header__top-bar h1 {
    text-align: left;
    font-size: 3.5rem;
  }
}
.header__top-bar-menu {
  align-items: center;
  display: flex;
  justify-content: center;
}
.header__top-bar-menu ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.header__top-bar-menu ul li {
  color: var(--font-menu-color);
  margin: 0 0.25rem;
}
.header__top-bar-icons {
  align-items: center;
  display: flex;
  justify-content: center;
}
@media (min-width: 740px) {
  .header__top-bar-icons {
    justify-content: flex-start;
  }
}
.header__top-bar-icons p {
  color: var(--font-menu-color);
}

:root {
  --background-circle: var(--gray-500);
}

.home__circles {
  display: flex;
  align-items: center;
  height: 12rem;
}
.home__circles-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  gap: 0.65rem;
}
@media (min-width: 740px) {
  .home__circles-content {
    gap: 0.25rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.home__circles-content-area {
  background-color: var(--background-circle);
  border-radius: 500px;
  height: 70px;
  width: 70px;
  display: block;
  margin: auto;
}

:root {
  --background: var(--gray-600);
  --font-color-text: var(--gray-500);
}

.home__about {
  background-color: var(--background);
  height: auto;
}
@media (min-width: 740px) {
  .home__about {
    height: 25rem;
  }
}
.home__about-row {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  padding: 4.5rem 0.85rem 2.5rem;
}
@media (min-width: 740px) {
  .home__about-row {
    padding: 0 0.85rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 740px) {
  .home__about-row-text {
    margin-top: 5rem;
  }
}
.home__about-row-text h2 {
  font-size: 2.3rem;
  margin: 0.6rem 0;
}
.home__about-row-text h3 {
  font-size: 0.95rem;
  margin: 0;
}
.home__about-row-text p {
  font-size: 1rem;
  line-height: 1.25rem;
}
.home__about-row-text h2,
.home__about-row-text h3,
.home__about-row-text p {
  color: var(--font-color-text);
}
.home__about-row-card {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: 1.55rem;
}
.home__about-row-card-content {
  width: 150px;
}
.home__about-row-card-content img {
  border-radius: 0.25rem;
}
.home__about-row-card-content h3 {
  color: var(--font-color-text);
  text-align: center;
  margin-bottom: 0.25rem;
}
.home__about-row-card-content p {
  color: var(--font-color-text);
  text-align: center;
}

.home__products {
  height: auto;
  padding: 0.75rem 0.5rem;
}
@media (min-width: 740px) {
  .home__products {
    height: 45rem;
  }
}
.home__products .container {
  height: 100%;
}
.home__products-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}
@media (min-width: 740px) {
  .home__products-content {
    flex-direction: row;
  }
}
.home__products-left {
  display: flex;
  justify-content: center;
}
@media (min-width: 740px) {
  .home__products-left {
    display: inherit;
  }
}
.home__products-left-card {
  width: 16rem;
}
@media (min-width: 740px) {
  .home__products-left-card {
    width: 19.58rem;
  }
}
.home__products-left-card-text {
  color: var(--gray-500);
  display: none;
  padding: 0 0.65rem;
  position: relative;
  top: -18rem;
}
.home__products-left-card-text h3 {
  font-size: 1.35rem;
  margin-bottom: 0.35rem;
}
.home__products-left-card-text p {
  font-size: 1.2rem;
}
.home__products-left-card img {
  height: 32rem;
  border-radius: 0.25rem;
  transition: all 0.5s;
}
.home__products-left-card img:hover {
  transform: scale(0.95);
  filter: brightness(0.25);
}
.home__products-left-card img:hover + .home__products-left-card-text {
  display: block;
}
.home__products-right-card {
  align-items: start;
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  margin-left: 0.85rem;
  justify-content: flex-start;
}
.home__products-right-card h2 {
  color: var(--gray-500);
  font-size: 2rem;
  margin-bottom: 0.75rem;
}
.home__products-right-card p {
  color: var(--gray-500);
  font-size: 1.25rem;
  line-height: 1.35rem;
  margin-bottom: 0.85rem;
}
.home__products-right-card button {
  background-color: var(--gray-600);
  border: none;
  border-radius: 0.45rem;
  color: var(--gray-500);
  font-weight: 700;
  padding: 0.45rem 1rem;
  transition: filter 0.5s;
}
.home__products-right-card button:hover {
  cursor: pointer;
  filter: brightness(0.95);
}

.home__social {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--gray-600);
  padding: 5rem 0;
}
.home__social-title {
  color: var(--gray-500);
  font-size: 2.56rem;
  text-align: center;
}
.home__social-content {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0.56rem;
  margin-top: 1.52rem;
}
@media (min-width: 740px) {
  .home__social-content {
    grid-template-columns: repeat(3, 1fr);
  }
}
.home__social-content-grid {
  align-items: center;
  display: flex;
  justify-content: center;
  height: auto;
}
@media (min-width: 740px) {
  .home__social-content-grid {
    height: 20rem;
  }
}
.home__social-content-grid:nth-child(1) {
  align-items: start;
}
.home__social-content-grid:nth-child(2) {
  align-items: end;
}
.home__social-content-grid:nth-child(3) {
  align-items: start;
}
.home__social-content-grid img {
  border-radius: 0.25rem;
}

:root {
  --footer-color: var(--gray-500);
  --background-button: var(--gray-800);
}

.footer__bar-third-social .instagram-icon, .footer__bar-third-social .facebook-icon {
  height: 2rem;
  transition: 0.5s;
  margin: 0.35rem 0;
}

.footer__bar-third-social .instagram-icon:hover, .footer__bar-third-social .facebook-icon:hover {
  cursor: pointer;
  filter: brightness(0.55);
}

.footer {
  padding-top: 1.5rem;
}
.footer__bar {
  padding: 0 0.85rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 0.65rem;
}
@media (min-width: 540px) {
  .footer__bar {
    grid-template-columns: 1fr 2fr;
  }
}
@media (min-width: 740px) {
  .footer__bar {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
.footer__bar h2 {
  font-size: 2rem;
  color: var(--footer-color);
}
.footer__bar-first-menu {
  margin-top: 0.75rem;
}
.footer__bar-first-menu ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.25rem;
}
.footer__bar-first-menu ul li {
  color: var(--footer-color);
  font-size: 1rem;
}
.footer__bar-second-input {
  margin-top: 0.75rem;
  padding-bottom: 0.85rem;
  border-bottom: 1px solid var(--footer-color);
}
.footer__bar-second-input form {
  width: 100%;
}
.footer__bar-second-input #email {
  background-color: transparent;
  border: 0;
  color: var(--footer-color);
  font-size: 1rem;
  width: 75%;
}
@media (min-width: 540px) {
  .footer__bar-second-input #email {
    width: 60%;
  }
}
@media (min-width: 740px) {
  .footer__bar-second-input #email {
    width: 78%;
  }
}
.footer__bar-second-input #buttonSubmit {
  background-color: var(--background-color);
  border: 1px solid var(--footer-color);
  border-radius: 0.25rem;
  color: var(--footer-color);
  transition: filter 0.2s;
  width: 20%;
  padding: 0.25rem 0;
  font-weight: 700;
  font-size: 1rem;
}
.footer__bar-second-input #buttonSubmit:hover {
  cursor: pointer;
  filter: brightness(0.5);
}
.footer__bar-third-social {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 0.75rem;
}
.footer__bar-third-social .facebook-icon:hover {
  transform: scale(1.5) rotate(-25deg);
}
.footer__bar-third-social .instagram-icon:hover {
  transform: scale(1.5) rotate(25deg);
}
.footer__vnda {
  padding-top: 1.5rem;
}
.footer__vnda p {
  color: var(--gray-500);
  text-align: center;
}
.footer__my {
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__my p {
  font-size: 0.95rem;
  color: var(--footer-color);
}
.footer__my p a {
  color: currentColor;
  text-decoration: none;
  transition: filter 0.2s;
}
.footer__my p a:hover {
  filter: brightness(0.5);
}
.footer__my img {
  height: 1rem;
  margin: 0 0.25rem;
}